/* // Small devices (landscape phones, less than 768px) */
@media (max-width: 575.98px) { 

    .add-to-do-input, .search-to-do-input, .app-top-button {
        padding-left: 2px;
        font-size: 9px;
    }
    
    .edit-field-container > .edit-item-container {
        column-gap: 4px;
    }

    .to-do-edit-component {
        padding-left: 2px;
        font-size: 9px;
    }

    .to-do-list-container > .to-do-item {
        padding: 11px 0px 11px 2px;
        font-size: 12px;
    }

    .to-do-item-text {
        font-size: 10px;
    }

    .edit-to-do-input{
        font-size: 10px;
    }
}
